import { Component } from '@angular/core';
import { AlertController,NavController, NavParams } from 'ionic-angular';

/**
 * Generated class for the ProfroutePage page.
 *
 * See http://ionicframework.com/docs/components/#navigation for more info
 * on Ionic pages and navigation.
 */

@Component({
  selector: 'page-profroute',
  templateUrl: 'profroute.html',
})
export class ProfroutePage {

  testRadioOpen: boolean;
  testRadioResult;

  constructor(public navCtrl: NavController, public navParams: NavParams, public alertCtrl:AlertController) {
  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad ProfroutePage');
  }

  yearRadio() {
    let alert = this.alertCtrl.create();
    alert.setTitle('Lightsaber color');

    alert.addInput({
      type: 'radio',
      label: 'Year I',
      value: 'year1',
      checked: true
    });

    alert.addInput({
      type: 'radio',
      label: 'Year II',
      value: 'year2'
    });

    alert.addInput({
      type: 'radio',
      label: 'Year III',
      value: 'year3'
    });

    alert.addInput({
      type: 'radio',
      label: 'Year IV',
      value: 'year4'
    });

    alert.addButton('Cancel');
    alert.addButton({
      text: 'Ok',
      handler: data => {
        console.log('Radio data:', data);
        this.testRadioOpen = false;
        this.testRadioResult = data;
      }
    });

    alert.present().then(() => {
      this.testRadioOpen = true;
    });

	}

	deptRadio() {
    let alert = this.alertCtrl.create();
    alert.setTitle('Lightsaber color');

    alert.addInput({
      type: 'radio',
      label: 'CSE',
      value: 'cse',
      checked: true
    });

    alert.addInput({
      type: 'radio',
      label: 'IT',
      value: 'it'
    });

    alert.addInput({
      type: 'radio',
      label: 'ECE',
      value: 'ece'
    });

    alert.addInput({
      type: 'radio',
      label: 'EEE',
      value: 'eee'
    });

    alert.addInput({
      type: 'radio',
      label: 'MECH',
      value: 'mech'
    });

    alert.addButton('Cancel');
    alert.addButton({
      text: 'Ok',
      handler: data => {
        console.log('Radio data:', data);
        this.testRadioOpen = false;
        this.testRadioResult = data;
      }
    });

    alert.present().then(() => {
      this.testRadioOpen = true;
    });

}
}


